}

// ÈÄâÊã©Á≠õÈÄâÈ°π
function selectOption(type, value) {
  if (type === 'domain') {
    if (value === null) {
      selectedDomain.value = { field_id: 0, field_name: 'ÂÖ®ÈÉ®' }
	  Params.page=1
	  Params.field_id=selectedDomain.value.field_id
      listarticles.getlistpolicy(Params)
    } else {
      selectedDomain.value = value
	  Params.page=1
	  Params.field_id=selectedDomain.value.field_id
      listarticles.getlistpolicy(Params)
    }
  }

  if (type === 'time') {
	  console.log(value)
    selectedTime.value = value
	if (value === 'ÊúÄËøë‰∏ÄÂë®') {
	      // ËÆ°ÁÆóÂπ∂Êõ¥Êñ∞‰∏∫ÊúÄËøë‰∏ÄÂë®
