	    } else {
	      // Ê∏ÖÁ©∫Êó∂Èó¥Á≠õÈÄâ
	      Params.release_time = '';
	    }
	
	    Params.page = 1;
	    listarticles.getlistpolicy(Params);
  }

  currentDropdown.value = null
}

