  } else {
    console.log('Êù•Ê∫êÔºöÁî®Êà∑ÁÇπÂáª tabBar ËøõÂÖ•');
	isselected.value=0
	Params.page=1
	listarticles.getlistpolicy(Params)
	Params.article_type="NEWS"
